<template>
  <div >
    <div class="nav">
      <Nav></Nav>
    </div>
    <div class="body">
      <!-- 弹性滚动条 -->
      <div class="wrapper" ref="wrapper">
        <ul class="content">
          <!-- 轮播图 -->
          <LunBo :lwarps="home.warpImgs"></LunBo>
          <!-- 主页标题分类 -->
          <home-title :htinfo="home.types"></home-title>
          <!-- 促销分类 -->
          <sales-promotion :salespros="home.bebefit"></sales-promotion>
          <!-- 热销分类 -->
          <hot-type :hotinfo="home.hotType"></hot-type>
          <!-- 猜你喜欢 -->
          <likes :likes="home.likes"></likes>
        </ul>
      </div>
    </div>
    <div class="bottom">
      <Bottom></Bottom>
    </div>
    
    
  </div>
</template>

<script>
import Nav from "./Nav";
import Bottom from "./Bottom";
import BScroll from "better-scroll";
import LunBo from "./LunBo";
import HomeTitle from "./HomeTitle";
import SalesPromotion from "./SalesPromotion";
import HotType from "./HotType";
import Likes from "./Likes";

import { mapState, mapMutations, mapActions, mapGetters } from "vuex";

export default {
  components: {
    LunBo,
    HomeTitle,
    SalesPromotion,
    HotType,
    Likes,
    Nav,
    Bottom
  },
  computed: {
    ...mapState(["home"])
  },
  methods: {
    initScroll() {
      this.$nextTick(() => {
        this.scroll = new BScroll(this.$refs.wrapper, {
          click:true
        });
      });
    },
    ...mapActions(["loadHomeData"]),
    ...mapActions(["loadLikesData"])
  },
  mounted() {
    this.initScroll();
    this.loadHomeData();
    this.loadLikesData();
  }
};
</script>
<style lang="less" scoped>
.nav {
  height: 80px; /*px*/
  // background: pink;
}
.body {
  position: absolute;
  top: 80px; /*px*/
  bottom: 80px; /*px*/
  left: 0px;
  right: 0px;
  .wrapper {
    position: absolute;
    top: 0px;
    bottom: 0;
    right: 0;
    left: 0;
  }
}
.bottom {
  height: 80px; /*px*/
  // background: #ccc;
  position: fixed;
  bottom: 0px; /*px*/
}
</style>



