<template>
    <!-- 分类产品细节 -->
    <div class="tdnew">
        <div class="nav">
            <Nav></Nav>
        </div>
        <!-- 头部不同排序 -->
        <div class="typedetail-title">
            <div :class="type.typeDetailSort | chooseSort('pop')"  @click="loadSort('pop')">综合</div>
            <div :class="type.typeDetailSort | chooseSort('sell')" @click="loadSort('sell')">销量</div>
            <div :class="type.typeDetailSort | chooseSort('new')" @click="loadSort('new')">新品</div>
        </div>
        <!-- 各类型具体产品 -->
        <div class="wrapper" ref="detail">
            <ul class="content">
                <div class="type-detail">
                    <div class="td-info" v-for="(v, i) of type.typeDetailNew" :key="'typedetail'+i" @click="loadCommodity(v)">
                        <div class="img">
                            <img :src="v.img" alt="">
                        </div>
                        <div class="title-props">
                             <div class="props" v-for="(vp, i) of v.props" :key="i">
                                {{ vp }}
                            </div>
                        </div>
                       
                        <div class="hot-bottom">
                            <b>￥{{ v.price }}</b>
                            <span>{{ v.cfav }} ☆</span>
                        </div>
                    </div>
                </div>
            </ul>
        </div>

    </div>
</template>
<script>
import {mapState, mapMutations, mapGetters, mapActions} from 'vuex';
import BScroll from 'better-scroll';
import Nav from "../Nav";

export default {
    data(){
        return{
            params:null
        }
    },
    components:{
        Nav
    },
    computed:{
        ...mapState(['type'])
    },
    methods:{
        ...mapActions(["loadTypesDetailNewMore","loadTypesDetailSearch"]),
        initScroll() {
            this.$nextTick(() => {
                this.scroll = new BScroll(this.$refs.detail, {
                    click:true
                })
            })
        },
        loadSort(value){
            let params = this.$route.params;
            if(params.fcid){
                this.loadTypesDetailNewMore({
                    title:params.result,
                    fcid:params.fcid,
                    sort:value
                });                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                            
            }else{
                this.loadTypesDetailSearch({
                    sort:value,
                    q:params.result
                });
            }
        },
        loadCommodity(value){
            this.$router.push({path:"/commodity/"+value.tradeItemId,query:{value}});
        }
    },
    mounted(){
        this.initScroll();
        let params = this.$route.params;
        if(params.fcid){
            this.loadTypesDetailNewMore({
                title:params.result,
                fcid:params.fcid,
                sort:"pop"
            });
        }else{
            this.loadTypesDetailSearch({
                sort:"pop",
                q:params.result
            });
        }
    }    
}
</script>
<style lang="less" scoped>

.tdnew{
    .nav {
       height: 80px; /*px*/ 
    }
    .typedetail-title{
        display: flex;
        justify-content: space-around;
        width: 100%;
        div{
            border-left: 1px solid #E6E6E6;
            width: 33.33%;
            text-align: center;
            font-size: 26px;/*px*/
            margin: 20px auto;
        }
        .show{
            color: #FC5979;
        }
        border-top: 1px/*px*/ solid #E6E6E6;
        border-bottom: 1px/*px*/ solid #E6E6E6;
        margin-bottom: 10px;/*px*/
    }
    .wrapper{
        width: 100%;
        position:absolute;
        top:170px;/*px*/
        bottom:0;
        right:0;
        left:0;
        .type-detail{
            display: flex;
            justify-content: space-around;
            flex-wrap: wrap;
            .td-info{
                width:50%;
                .img{
                    width:100%;
                    height: 75%;
                    text-align: center;              
                    img{
                        width:95%;
                        height:100%;
                        border-radius: 10px;/*px*/
                    }
                }
                .title-props{
                    display: flex;
                    justify-content: flex-start;
                    flex-wrap: wrap;
                    height: 14%;
                    .props{
                        color:#666;
                        height:30px;/*px*/
                        line-height: 30px;/*px*/
                        text-align: center;
                        background: #EFF3F6;
                        margin: 5px;
                        padding:5px;

                    }
                }
                .hot-bottom{
                    height: 60px;/*px*/
                    display: flex;
                    justify-content: space-between;
                    width:85%;
                    margin-left: 7%;
                    b{
                        color: #FC5979;
                        font-size: 30px;/*px*/
                    }
                    span{
                        color: #ADADAD;
                        height:45px;/*px*/
                        line-height: 45px;/*px*/
                    }
                }
            }
        }
    }
}
</style>



