import api from '../../server/api';

const state = {
    typesAll:[],
    typesMore:[],
    typeDetail:[],
    typeDetailFcid:[],
    typeDetailNew:[],
    typeDetailNewFcid:[],
    typeDetailSort:"pop"
}

const getters = {

}

const actions = {
    // 类型左侧数据
    loadTypesData ({ commit }) {
        api.loadDataByType({
            appPlat: "m",
            pids:"41789"
        }).then(({ data })=>{
            commit("setTypesAll", data[41789].list);
        })
    },
    // 分类右侧上方数据
    loadTypesMoreData ({ commit }, pid="41888") {
        api.loadDataByTypeMore({
            appPlat: "m",
            pid
        }).then(({ data })=>{
            commit("setTypesMore", data.categoryNavigation.list);
        })
    },
    // 分类右侧下方数据 和 fcid 、sort
    loadTypesDetail ({ commit }, fcid="50243") {
        api.loadDataByTypeDetail({
            cKey: "h5-cube",
            page: 1,
            sort: "pop",
            fcid
        }).then(({ result })=>{
            commit("setTypesDetail", result.wall.docs);
            commit("setTypeDetailFcid", result.param.fcid);
        })
    },
    // 右侧下方数据根据sort变化
    loadTypesDetailMore({ commit }, params={sort:"pop", fcid:"50243"} ){
        api.loadDataByTypeDetail({
            cKey: "h5-cube",
            page: "1",
            fcid:params.fcid,
            sort:params.sort
        }).then(({ result })=>{
            commit("setTypesDetail", result.wall.docs);
            commit("setTypeDetailSort", result.param.sort);
        })
    },
    // 新页面数据不同类型排序
    loadTypesDetailNewMore({ commit }, params={title: "", fcid: "",sort:""}){
        api.loadDataByTypeDetail({
            cKey: "16",
            sort: params.sort,
            page: "1",
            title: params.title,
            fcid:params.fcid
        }).then(({ result })=>{
            commit("setTypesDetailNew", result.wall.docs);
            commit("setTypesDetailNewFcid", result.param.fcid);
            commit("setTypeDetailSort", result.param.sort);
        })
    },
    // 产品列表 搜索跨页面
    loadTypesDetailSearch({ commit },params={sort:"",q:""}){
        console.log(params.q);
        api.loadLikes({
            cKey: "46",
            page: "1",
            sort:params.sort,
            q:params.q
        }).then(({ result }) => {
            commit("setTypesDetailNew", result.wall.docs);
            commit("setTypesDetailNewFcid", result.param.fcid);
            commit("setTypeDetailSort", result.param.sort);
        })
    }
}

const mutations = {
    setTypesAll(state, data){
        state.typesAll = data;
    },
    setTypesMore(state, data){
        state.typesMore = data;
    },
    setTypesDetail(state, data){
        state.typeDetail = data;
    },
    setTypeDetailFcid(state, data){
        state.typeDetailFcid = data;
    },
    setTypesDetailNew(state, data) {
        state.typeDetailNew = data;
    },
    setTypesDetailNewFcid(state, data) {
        state.typeDetailNewFcid = data;
    },
    setTypeDetailSort(state, data) {
        state.typeDetailSort = data;
    },
}

export default{
    state,
    actions,
    getters,
    mutations
}


