<template>
    <div class="salespromotion">
        <div class="title">
            <img src="../../static/img/salesprotitle.jpg" alt="">
        </div>
        <div class="sales-main">
            <div :class="'salsep'+i" v-for="(v,i) of salespros" :key="'salesp'+i">
                <div>
                    <h1>{{ v.title }}</h1>
                    <p class="viceTitle">{{ v.viceTitle | deleteOther }}</p>
                </div>
                <img :src="v.image" alt="">
                
            </div>
        </div>
    </div>
</template>
<script>
export default {
    props:{
        salespros:null
    },
    filters:{
        deleteOther(data){
            return data.replace(/(^{*)|(}*$)/g, "");
        }
    }
}
</script>
<style lang="less" scoped> 
.salespromotion{
    width:100%;
    height:600px;/*px*/
    .viceTitle{
        color:#FD6A83;
    }
    .title{
        text-align: center;
        img{
            width:80%;
        }
    }
    .sales-main{
        position: relative;
        width:100%;
        height: 600px;/*px*/

        .salsep0,.salsep1,.salsep2,.salsep3,.salsep4,.salsep5{
            border:1px/*px*/ solid #ccc;
        }
        .salsep0{
            width:50%;
            position: absolute;
            height:280px;/*px*/
            text-align: center;
            h1{
                font-size: 30px;/*px*/
                margin-top: 30px;/*px*/
            }
            img{
                height:62%;
            }
        }
        .salsep1{
            width: 50%;
            height: 140px;/*px*/
            position: absolute;
            left: 50%;
        }
        .salsep2{
            width: 50%;
            height: 140px;/*px*/
            position: absolute;
            left: 50%;
            top:140px;/*px*/
        }
        .salsep1,.salsep2{
            display: flex;
            justify-content: space-around;
            h1{
                font-size: 30px;/*px*/
                margin-top: 30px;/*px*/
            }
        }
        .salsep3,.salsep4,.salsep5{
            height:280px;/*px*/
            text-align: center;
            h1{
                font-size: 30px;/*px*/
                margin-top: 20px;/*px*/
            }
            img{
                width:60%;
                margin-top: 10px;/*px*/
            }
        }
        .salsep3{
            width:33.33%;
            position: absolute;
            top:280px;
        }
        .salsep4{
            width:33.33%;
            position: absolute;
            top:280px;
            left:33.33%;
        }
        .salsep5{
            width:33.33%;
            position: absolute;
            top:280px;
            left:66.66%;
        }
    }
}
</style>



