<template>
    <div class="types">
        <div class="nav">
            <Nav></Nav>
        </div>
        <div class="body">
            <div class="types-left">
                <div class="wrapper" ref="left">
                    <ul class="content">
                        <types-left :typesall="type.typesAll" ></types-left>
                    </ul>
                </div>
            </div>
            <div class="types-right">
                <div class="wrapper" ref="right">
                    <ul class="content">
                    <types-right :typeinfo="type.typesMore" :typedetail="type.typeDetail" :fcid="type.typeDetailFcid" :sort="type.typeDetailSort"></types-right>
                    </ul>
                </div>
            </div>
        </div>
        <div class="bottom">
            <Bottom></Bottom>
        </div>
    </div>
</template>
<script>
import Nav from "../Nav";
import Bottom from "../Bottom";
import TypesLeft from "./TypesLeft";
import TypesRight from "./TypesRight";
import BScroll from "better-scroll";

import { mapState, mapMutations, mapGetters, mapActions } from "vuex";
export default {
  components: {
    TypesLeft,
    TypesRight,
    Nav,
    Bottom
  },
  computed: {
    ...mapState(["type"])
  },
  methods: {
    ...mapActions(["loadTypesData"]),
    ...mapActions(["loadTypesMoreData"]),
    ...mapActions(["loadTypesDetail"]),
    initScroll() {
      this.$nextTick(() => {
        this.scroll = new BScroll(this.$refs.left, {
          click: true
        });
      });
    },
    initScrollRight() {
      this.$nextTick(() => {
        this.scroll = new BScroll(this.$refs.right, {
          click: true
        });
      });
    }
  },
  mounted() {
    this.loadTypesData();
    this.initScroll();
    this.initScrollRight();
    this.loadTypesMoreData();
    this.loadTypesDetail();
  }
};
</script>
<style lang="less" scoped>
.nav {
  height: 80px; /*px*/
  // background: pink;
}
.body {
  position: absolute;
  top: 80px; /*px*/
  bottom: 80px; /*px*/
  left: 0px;
  right: 0px;
  .types-left {
    width: 24%;
    height: 100%;
    .wrapper {
      height: 100%;
      width: 24%;
      position: absolute;
      top: 0px;
      bottom: 0;
      right: 0;
      left: 0;
    }
  }
  .types-right {
    width: auto;
    margin-left: 24%;
    .wrapper {
      height: 100%;
      width: auto;
      position: absolute;
      top: 0px;
      bottom: 0;
      right: 0;
      left: 24%;
    }
  }
}
.bottom {
  height: 80px; /*px*/
  // background: #ccc;
  position: fixed;
  bottom: 0px; /*px*/
}
</style>



