<template>
    <div class="hottype">
        <div class="titleimg">
            <img src="../../static/img/hotTyle.jpg" alt="">
        </div>
        <div class="hots">
            <div class="hotexmple" v-for="(v,i) of hotinfo" :key="'hot'+i">
                <img :src="v.image" alt=""><br>
                <span >{{ v.title }}</span>    
            </div>
        </div>
    </div>
</template>
<script>
export default {
    props:{
        hotinfo:null
    }
}
// console.log(this.hotinfo);
</script>
<style lang="less" scoped>
.titleimg{
        text-align: center;
        img{
            width:80%;
        }
}
.hots{
    margin:20px 0 20px 0;/*px*/
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    // height:370px;/*px*/
    // line-height: 40px;/*px*/
    .hotexmple{
        width: 25%;
        text-align: center;
        img{
            width:70%;
        }
        span{
            width:100%;

        }
    }
}
</style>



