<template>
    <div class="types-more">
        <div class="typeinfo" v-for="(v, i) of typeinfo" :key="'typeinfo'+i" @click="loadTheFcid(v.title,v.link)">
            <div class="img">
                <img :src="v.image" alt="">
            </div>
            <h1>{{v.title}}</h1>
        </div>
        <div class="typeinfo" v-for="i of 3-(typeinfo.length%3)" :key="i">
        </div>
        <div class="typedetail-title">
            <div :class="sort | chooseSort('pop')" @click="loadTypesDetailMore({sort:'pop', fcid })">综合</div>
            <div :class="sort | chooseSort('sell')" @click="loadTypesDetailMore({sort:'sell', fcid})">销量</div>
            <div :class="sort | chooseSort('new')" @click="loadTypesDetailMore({sort:'new', fcid})">新品</div>
        </div>
        <div class="type-detail">
            <div class="td-info" v-for="(v, i) of typedetail" :key="'typedetail'+i" @click="loadCommodity(v)">
                <div class="img">
                    <img :src="v.img" alt="">
                </div>
                <h1>{{ v.title }}</h1>
                <div class="hot-bottom">
                    <b>￥{{ v.price }}</b>
                    <span>{{ v.cfav }} ☆</span>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { mapActions } from 'vuex';
export default {
    props:{
        typeinfo:null,
        typedetail:null,
        fcid:null,
        sort:null
    },
     methods:{
        //  打开产品列表
         loadTheFcid(title,url){
            let fcid = url.substring(url.indexOf("=") + 1, url.indexOf("&"));
            this.$router.push({path:"/detail/"+title+"/"+fcid});
         },
        ...mapActions(["loadTypesDetailMore"]),
        // 打开产品详情页
        loadCommodity(value){
            this.$router.push({path:"/commodity/"+value.tradeItemId,query:{value}});
        }
        
    }
}
</script>
<style lang="less" scoped>
.types-more{
    width:100%;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    .typeinfo{
        margin-top:50px;/*px*/
        width:33.33%;
        text-align: center;
        .img{
            width:100%;
            img{
                width:70%;
            }
        }
        h1{
            margin-top: 10px;/*px*/
            font-size: 24px;/*px*/
            color:#878282;
        }
    }
    .typedetail-title{
        display: flex;
        justify-content: space-around;
        width: 100%;
        div{
            border-left: 1px solid #E6E6E6;
            width: 33.33%;
            text-align: center;
            font-size: 26px;/*px*/
            margin: 20px auto;
        }
        .show{
            color: #FC5979;
        }
        border-top: 1px/*px*/ solid #E6E6E6;
        border-bottom: 1px/*px*/ solid #E6E6E6;
        margin-bottom: 10px;/*px*/
    }
    .type-detail{
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;
        .td-info{
            width:50%;
            .img{
                width:100%;
                height: 78%;
                text-align: center;              
                img{
                    width:90%;
                    height: 100%;
                    border-radius: 10px;/*px*/
                }
            }
            h1{
                font-size: 26px;/*px*/
                height: 40px;/*px*/
                line-height: 40px;/*px*/
                // color: #84939A;
                text-align: left;
                text-indent: 10px;/*px*/
                width:90%;
                margin-left: 7%;
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 1;
                -webkit-box-orient: vertical;
            }
             .hot-bottom{
                height: 60px;/*px*/
                display: flex;
                justify-content: space-between;
                width:85%;
                margin-left: 7%;
                b{
                    color: #FC5979;
                    font-size: 30px;/*px*/
                }
                span{
                    color: #ADADAD;
                    height:45px;/*px*/
                    line-height: 45px;/*px*/
                }
            }
        }
    }
}
</style>



