import api from '../../server/api';

const state = {
    commodifyCommont:[],
    commodifyTag:[],
    commodifyTotal:""
}

const getters = {

}

const actions = {
    loadCommodifyInfo({ commit }, itemId=1){
        api.loadCommont({
            type:"1",
            itemId,
            appPlat:"m"

        }).then(({ data })=>{
            console.log(data);
            commit("setCommodifyCommont", data.list);
            commit("setCommodifyTag", data.rateTags);
            commit("setCommodifyTotal", data.total);
        })
    }
}

const mutations = {
    setCommodifyCommont(state, data){
        state.commodifyCommont = data;
    },
    setCommodifyTag(state, data) {
        state.commodifyTag = data;
    },
    setCommodifyTotal(state, data){
        state.commodifyTotal = data;
    }
}

export default {
    state,
    actions,
    getters,
    mutations
}


