import Vue from 'vue';
import Router from 'vue-router';
import Home from '../components/Home';
import TypesComponent from '../components/Types/TypesComponent';
import TypesDetail from '../components/Types/TypesDetail';
import SearchInfo from '../components/Search/SearchInfo'
import CommodityComponent from '../components/Commodity/CommodityComponent'
import ShopMall from '../components/ShopMall'

Vue.use(Router)

export default new Router({
  mode:"history",
  routes: [
    { path: '/', redirect:"/shopmall"},
    { path:'/shopmall', component:ShopMall,children:[
      { path:'/',redirect:'home'},
      { path: 'home', name: 'Home', component: Home },
      { path: 'types', component: TypesComponent }
    ] },
    { path: "/detail/:result/:fcid?", component: TypesDetail },
    { path: "/search", component: SearchInfo },
    { path: '/commodity/:value', component: CommodityComponent }

  ]
})



