import api from '../../server/api';

const state = {
    searchHot:[],
    searchSketch:"",
    searchMore:[]
}

const getters = {

}

const actions = {
    loadSearch({ commit }){
        api.loadSearchData({
            platdorm:"H5",
            code:"hotWord,sketch"
        }).then(({ data })=>{   
            commit("setSearchWord", data.hotWord.data);
            commit("setSearchSketch", data.sketch.data);
        })
    },
    loadSearchMore({ commit }, q="上衣"){
        api.loadLikes({
            cKey:"46",
            page:"1",
            sort:"pop",
            q
        }).then(({ result })=>{
            commit("setSearchMore", result.wall.docs);
        })
    }
}

const mutations = {
    setSearchWord(state, data){
        state.searchHot = data;
    },
    setSearchSketch(state, data){
        state.searchSketch = data;
    },
    setSearchMore(state, data){
        state.searchMore = data;
    }
}

export default{
    state,
    actions,
    getters,
    mutations
}


