<template>
    <div class="likes">
        <div class="titleimg">
            <img src="../../static/img/likes.jpg" alt="">
        </div>
        <div class="likes-info">
            <div class="like-example" v-for="(v,i) of likes" :key="'like'+i" @click="loadCommodity(v)">
                <div class="img">
                    <img :src="v.img" alt="">
                </div>
                <h1>{{ v.title }}</h1>
                <div class="hot-bottom">
                    <b>￥{{ v.price }}</b>
                    <span>{{ v.cfav }} ☆</span>
                </div>
                 
            </div>
        </div>
    </div>
</template>  
<script>
export default {
    props:{
        likes:null
    },
    methods:{
        loadCommodity(value){
            console.log(111);
            this.$router.push({path:"/commodity/"+value.tradeItemId,query:{value}});
        }
    }
}
</script>
<style lang="less" scoped>
.titleimg{
        text-align: center;
        img{
            width:80%;
        }
}
.likes-info{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    padding-bottom: 70px;
    .like-example{
        width:50%;
        text-align: center;
        .img{
            height:550px;/*px*/
            width:100%;
            img{
                margin:auto 0px;
                height:100%;
                width:95%;
            }
        }
        h1{
            font-size: 26px;/*px*/
            color: #84939A;
            text-align: left;
            text-indent: 26px;/*px*/
            width:90%;
            margin-left: 7%;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
        }
        .hot-bottom{
            height: 60px;/*px*/
            display: flex;
            justify-content: space-between;
            width:85%;
            margin-left: 7%;
            b{
                font-size: 26px;/*px*/
            }
            span{
                color: #ADADAD;
            }
        }
    }
}
</style>



