<template>
    <div class="hometitle">
        <div class="tmp" v-for="(v,i) of htinfo" :key="'ht'+i">
            <img :src="v.image" alt=""><br>
            <span >{{ v.title }}</span>    
        </div>
    </div>
</template>
<script>
    export default {
        props:{
            htinfo:null
        }
    }
</script>
<style lang="less" scoped>
.hometitle{
    margin:20px 0 20px 0;/*px*/
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    height:370px;/*px*/
    // line-height: 40px;/*px*/
    .tmp{
        width:20%;
        text-align: center;
        img{
            width:70%;
        }
        span{
            width:100%;

        }
    }
}
</style>



