<template>
    <div class="lunbotu">
        <swiper class="warp" :options="swiperOption" >
            <swiper-slide v-for="(v,i) of lwarps" :key="'warp'+i">
                <img :src="v.image_800" alt="" srcset="">
            </swiper-slide>
            <!-- Optional controls -->
            <div class="swiper-pagination"  slot="pagination"></div>
        </swiper>
    </div>
</template>
<script>
    import { swiper,swiperSide } from "vue-awesome-swiper";
    export default {
        
        data(){
            return{
                swiperOption:{
                    autoplay: 2000,
                    loop:true,
                    pagination : '.swiper-pagination',
                    paginationClickable :true
                }
            }
        },
        components:{
            swiper,
            swiperSide,
            pagination:'.swiper-pagination'
        },
        props:{
            lwarps:null
        }
    }
</script>
<style lang="less" scoped>
    .lunbotu{
        width:100%;
        height:auto;
        img{
            width:100%;
            height: auto;
        }
        .swiper-pagination{
            background-color: rgba(51, 51, 51, 0.4);
        }
    }

</style>



