let chooseFcid = function(value){
    let result = value.substring(value.indexOf("=") + 1, value.indexOf("&"));
    return result;
}

let chooseKey = function (value, key){
    return value==key?"show":"";
}

let chooseSort = function (value, key) {
    return value == key ? "show" : "";
}
export default {
    chooseFcid, chooseKey, chooseSort
}


