<template>
    <div class="types-all">
        <div class="type" v-for="(v, i) of typesall" :key="'type'+i" @click="loadRightmore(v)" >
            <li :class="v.maitKey | chooseKey(key)" class=""><p>{{ v.title }}</p></li>
        </div>
    </div>    
</template>
<script>
import {mapActions} from 'vuex';

export default {
    data(){
        return{
            key:41888
        }
    },
    props:{
        typesall:null
    },
    methods:{
        loadRightmore(v){
            this.loadTypesMoreData(v.maitKey);
            this.loadTypesDetail(v.miniWallkey);
            this.key = v.maitKey;
        },
        ...mapActions(["loadTypesMoreData"]),
        ...mapActions(['loadTypesDetail'])
    }
}
</script>
<style lang="less" scoped>
.types-all{
    // width:23%;
    background: #F6F6F6;
    .type{
        height: 100px;/*px*/
        line-height: 100px;/*px*/
        text-align: center;
        font-size: 30px;/*px*/
        color: #666666;   
        .show{
            background: white;
            color:#FC5979;
            height:100%;
            width: 100%;
            p{
                border-left:5px/*px*/ solid #FC5979;
                width:100%; 
                height: 50px;/*px*/
                line-height: 50px;/*px*/
                margin-top:25px;/*px*/
            }
        }     
    }
}
</style>



