<template>
    <div class="nav">
        <div class=" left">
            <i class="iconfont icon-xiaoxizhongxin"></i>
        </div>
        <div class="input" @click="loadSearch()">
            &nbsp;
            <i class="iconfont icon-sousuo"></i>
            <span>运动鞋</span>   
        </div>
        <div class="right">
            <i class="iconfont icon-gouwuche right"></i>
        </div>
    </div>
</template>
<script>
export default {
    methods:{
        loadSearch(){
            this.$router.push("/search");
        }
    }
}
</script>
<style lang="less" scoped>
    .nav{
        height: 80px;/*px*/
        display: flex;
        .left,.right{
            width:11%;
            font-size: 44px;/*px*/    
            height: 80px;/*px*/
            line-height: 80px;/*px*/   
            text-align: center;
        }
        .left i{
            font-size: 44px;/*px*/
        }
        .input{
            margin-top: 15px;/*px*/
            height: 50px;/*px*/
            width:78%;
            background: #eee;
            border:1px solid #ccc;
            border-radius: 10px;
            color:#ccc;
            i{
                // height: 30px;/*px*/
                // line-height: 30px;/*px*/
                font-size: 35px;/*px*/
                position: relative;
                top:5px;/*px*/
                // left:-5px;/*px*/
            }
            span{
                position: relative;
                top:5px;/*px*/
                left:5px;/*px*/
                font-size: 25px;/*px*/
            }

        }

    }

</style>



