<template>
    <div class="commodity wrapper" ref="commodity">
        <ul class="content">
            <div class="img">
                <img :src="query.img" alt="">
            </div>
            <h1>{{ query.title }}</h1>
            <div class="price">
                <div class="new">
                    <span class="rmb">￥</span>
                    <span class="newprice">{{ query.price }}</span>
                </div>
                <div class="old">
                    <div class="oldprice">
                        <del>
                            <span>￥</span>
                            <span> {{ query.orgPrice }}</span>
                        </del>
                    </div>
                    <div class="discount">
                        {{ Math.round(query.price/query.orgPrice*10) }}折
                    </div>
                </div>
            </div>
            <div class="postprice">
                免邮费
            </div>
            <div class="chengnuo">
                <div>
                    <span>72小时发货</span>
                </div>
                <div>
                    <span>7天无理由退货</span>
                </div>
                <div>
                    <span>退货补运费</span>
                </div>
            </div>
            <div class="total">
                <span>买家评价 {{ commodify.commodifyTotal }}</span>&nbsp;|&nbsp; 
                <span>销量 {{ query.sale}}</span>
            </div>
            <div class="commonet">
                <div class="comall">
                    <div class="comtop" v-for="(v, i) in commodify.commodifyTag" :key="i">
                        {{ v.value }}({{v.num}})
                    </div>
                </div>
                <div class="commont-info" v-for="(vc, i) of commodify.commodifyCommont" :key="i">
                    <div class="person">
                        <div class="img">
                            <img :src="vc.user.avatar" alt="">
                        </div>
                        <div class="name">
                            {{vc.user.uname}}
                        </div>
                    </div>
                    <div class="commonet-example">
                        {{ vc.content }}
                    </div>
                    <div class="time">
                        {{ vc.time }}&nbsp;{{ vc.style }} <br> {{ vc.extraInfo | deleteOther }}
                    </div>
                    <div class="com-img">
                        <div class="img" v-for="(vi, i) in vc.images" :key="i">
                            <img :src="vi" alt="">
                        </div>
                    </div>
                </div>
            </div>
        </ul>
    </div>    
</template>
<script>
import { mapState, mapMutations, mapActions, mapGetters } from 'vuex';
import BScroll from "better-scroll";
export default {
    data(){
        return{
            query:""
        }
    },
    filters:{
        deleteOther(data){
            console.log("===>",data);
            if(data){
                return data.toString().replace(/\[|\]/g,"");
            }
            return "";
        }
    },
    computed:{
        ...mapState(["commodify"])
    },
    methods:{
        initScroll() {
            this.$nextTick(() => {
                this.scroll = new BScroll(this.$refs.commodity, {
                    click:true
                })
            })
        },
        ...mapActions(['loadCommodifyInfo'])
    },
    mounted(){
        this.initScroll();        
        this.query = this.$route.query.value;
        let id = this.query.tradeItemId;
        console.log(id);
        this.loadCommodifyInfo(
            id
        );
    }
}
</script>
<style lang="less" scoped>
.commodity{
    width: 100%;
    position:absolute;
    top:0;/*px*/
    bottom:0;
    right:0;
    left:0;
    .img{
        width:100%;
        text-align: center;
        img{
            padding:5%;
            width:80%
        }
    }
    h1{
        font-size: 30px;/*px*/
        width:90%;
        margin-left: 5%;
    }
    .price{
        margin-left: 5%;
        display: flex;
        justify-content: flex-start;
        .new{
            .rmb{
                font-size:  25px;/*px*/
                position: relative;
                top:-20px;
            }   
            .newprice{
               font-size: 80px; /*px*/
            }
        }
        .old{
            margin-left: 5%;
            margin-top: 2%;
            .oldprice{
                font-size: 30px;/*px*/
                color:#999;
            }
        }
        .discount{
            color:#FC3A58;
            background:#FEE8EE;
            padding:10px;/*px*/
            width:40px;/*px*/
        }
    }
    .postprice{
        margin-left: 5%;
        color:#999;
        padding:3% 0;
        border-bottom: 1px/*px*/ solid #ccc;
    }
    .chengnuo{
        padding:3% 0;
        display: flex;
        justify-content: flex-start;
        padding-left:5%;
        border-bottom: 10px/*px*/ solid #ccc;
        div {
            margin-right:5%;
        }
    }
    .total{
        display: flex;
        justify-content: flex-start;
        padding:3% 0;
        display: flex;
        justify-content: flex-start;
        margin-left:5%;
        color:#999;
    }
    .commonet{
        margin-left: 5%;
        .comall{
            display: flex;
            justify-content: flex-start;
            flex-wrap: wrap;
            .comtop{
                color:#FC3A58;
                background:#FEE8EE;
                padding: 1%;
                margin: 1%;
            }
        }
        .commont-info{
            padding:5% 0;
            border-bottom: 1px/*px*/ solid #ccc;
            .person{
                display: flex;
                justify-content: flex-start;
                height:60px;/*px*/
                .img{
                    width:60px;/*px*/
                    height:60px;/*px*/   
                    border-radius: 60px;/*px*/                 
                    img{
                        width:100%;
                        height: 100%;
                    }
                }
                .name{
                    height:60px;/*px*/
                    line-height: 60px;/*px*/
                }
            }
            .commonet-example{
                color:#333;
                padding: 2% 5% 2% 0;
                line-height: 40px;/*px*/
                font-size: 26px;/*px*/
            }   
            .time{
                color:#999;
            }
            .com-img{
                display: flex;
                justify-content: flex-start;
                flex-wrap: wrap;
                .img{
                    width:20%;
                }
            }
        }
    }
}
</style>



