<template>
    <div class="search">
        <div class="top">
            <div class=" left"  @click="goBack()">
                <i class="iconfont icon-xiangzuojiantou"></i>
            </div>
            <input type="text" autofocus="autofocus" v-model="msg" />
            <div class="right" @click="loadSearchMore(msg)">
                <span>搜索</span>
            </div>
        </div>
        <div class="search-show">
            <div class="ss-top">
                <div class="top-left">
                    <i class="iconfont icon-sousuo"></i>
                    <span>历史搜索</span>
                </div>
                <div class="top-right" @click="deleteHistory()">
                    <i class="iconfont icon-lajixiang"></i>
                </div>
            </div>
            <div class="ss-main">
                <div class="search-info" v-for="(v, i) of searchValue" :key="i">
                    {{ v }}
                </div>
                <div class="no-search" v-if="!searchValue">暂无历史搜索</div>
            </div>
        </div>
        <div class="search-show">
            <div class="ss-top">
                <div class="top-left">
                    <i class="iconfont icon-shoucang"></i>
                    <span>热门搜索</span>
                </div>
                <div class="top-right">
                </div>
            </div>
            <div class="ss-main">
                <div class="search-info" v-for="(v, i) of search.searchHot" :key="'shot'+i" @click="loadSearchMore(v.query)">
                    {{v.query}}
                </div>
            </div>
        </div>
    </div>    
</template>
<script>
import {mapState, mapMutations, mapGetters, mapActions} from 'vuex';
export default {
    data(){
        return{
            msg:"",
            searchValue:""
        }
    },
    computed:{
        ...mapState(["search"])
    },
    methods:{
        ...mapActions(["loadSearch"]),
        loadSearchMore(value){
            // 跳转查询产品
            this.$router.push({path:"/detail/"+value});
            // 查询产品加入本地存储
            if(localStorage['searchKey']){
                if(value != null || value != "" ){
                    let storValue = localStorage['searchKey']+"."+value;
                    localStorage.setItem("searchKey", storValue);
                }
            }else{
                let storValue = value;
                localStorage.setItem("searchKey", storValue)
            }
        },
        loadLocalStorage(){
            if(localStorage['searchKey']){
                this.searchValue = localStorage['searchKey'].split(".");
            }
        },
        deleteHistory(){
            localStorage.removeItem("searchKey");
            this.searchValue = null;
        },
        goBack(){
            this.$router.go(-1);
        }
    },
    mounted(){
        this.loadSearch();
        this.loadLocalStorage();
        setTimeout(()=>{
            this.msg = this.$store.state.search.searchSketch.query;
        },1)
        

    }
};

</script>
<style lang="less" scoped>
.top {
  height: 80px; /*px*/
  display: flex;
  .left,
  .right {
    width: 11%;
    color: #666666;
    font-size: 30px; /*px*/
    height: 80px; /*px*/
    line-height: 80px; /*px*/
    text-align: center;
  }
  .left i {
    font-size: 44px; /*px*/
  }
  input {
    margin-top: 15px; /*px*/
    height: 50px; /*px*/
    width: 78%;
    font-size: 30px; /*px*/
    border: 1px solid #ccc;
    border-radius: 10px; /*px*/
    color: #ccc;
    text-indent: 20px; /*px*/
    outline: none;
    // focus:;
    &:focus {
      border: 1px solid #dd2449;
      outline: none;
    }
  }
}
.search-show{
    border-top: 1px solid #ccc;
    .ss-top{
        display: flex;
        justify-content: space-between;
        color:#999;
        height:80px;/*px*/
        line-height: 80px;/*px*/
        .top-left{
            width:50%;
            margin-left: 5%;
        }
        .top-right{
            width:10%;
        }
    }
    .ss-main{
        display: flex;
        justify-content: flex-start;
        flex-wrap: wrap;
        .search-info{
            border:1px/*px*/ solid #999;
            border-radius: 10px/*px*/;
            color:#666;
            height:30px;/*px*/
            line-height: 30px;/*px*/
            text-align: center;
            margin:30px;/*px*/
            padding:10px;/*px*/
        }
        .no-search{
            color:#999;
            height:30px;/*px*/
            line-height: 30px;/*px*/
            text-align: center;
            margin:30px;/*px*/
            padding:10px;/*px*/
        }
    }
}
</style>



