import jsonpUtil from '../util/jsonpUtil';

const mce = "https://mce.mogucdn.com/jsonp/";

const mcesearch = "https://list.mogujie.com/search";

const hotsearch = "https://list.mogujie.com/module/mget";

const commont = "https://rate.mogujie.com/h5/rate/jsonp/x6.rate.ratelist/v1";

export default {
    // 主页数据网址
    loadDataByPids(params){
        let url = mce + "multiget/3";
        return jsonpUtil.request(url, params);
    },
    // 主页喜欢模块网址
    loadLikes(params){
        let url = mcesearch;
        return  jsonpUtil.request(url, params);
    },
    // 分类左侧模块网址
    loadDataByType(params){
        let url = mce + "multiget/3";
        return jsonpUtil.request(url, params);
    },
    // 分类右侧
    loadDataByTypeMore(params){
        let url = mce + "makeup/3";
        return jsonpUtil.request(url, params);
    },
    // 分类具体信息
    loadDataByTypeDetail(params){
        let url = mcesearch;
        return jsonpUtil.request(url, params);
    },
    // 热门搜索
    loadSearchData(params){
        let url = hotsearch;
        return jsonpUtil.request(url, params);
    },
    loadCommont(params){
        let url = commont;
        return jsonpUtil.request(url, params);
    }


}


